import React from 'react'
import styles from './NotFound.module.css'
import { Link } from 'react-router-dom'

/**
 * Composant reprsentant une page d'erreur 404.
 *
 * @function
 * @returns {JSX.Element} - L'lment JSX reprsentant une page d'erreur 404.
 */
const NotFound = () => (
  <main className={`${styles.main} ${styles.bgDark}`}>
    <div className={styles.container}>
      {/* Titre de la page */}
      <h1 className={styles.title}>404 - Page non trouve</h1>
      {/* Message informatif */}
      <p className={styles.message}>
        Oups! La page que vous recherchez n'existe pas ou a t dplace.
      </p>
      <br />
      {/* Lien pour revenir  la page d'accueil */}
      <Link to="/" className={styles.homeLink}>
        Retourner  l'accueil
      </Link>
    </div>
  </main>
)

export default NotFound
