import { useState } from 'react'

/**
 * Hook personnalis pour grer les donnes de formulaire.
 *
 * @param {Object} initialState - tat initial des donnes du formulaire.
 * @returns {Array} - Retourne un tableau contenant les donnes, la fonction de gestion des changements et la fonction de rinitialisation.
 */
const useFormData = (initialState) => {
  const [data, setData] = useState(initialState)

  /**
   * Gre les changements dans les champs de formulaire.
   *
   * @param {Event} event - vnement dclench lors d'un changement dans un champ de formulaire.
   */
  const handleChange = (event) => {
    const { name, value } = event.target
    setData((prevData) => ({ ...prevData, [name]: value }))
  }

  /**
   * Rinitialise les donnes du formulaire  leur tat initial.
   */
  const resetFormData = () => {
    setData(initialState)
  }

  return [data, handleChange, resetFormData]
}

export default useFormData
