import { configureStore } from '@reduxjs/toolkit'
import employeeReducer from '../slices/employeeSlice'

/**
 * Tente de rcuprer la liste des employs stocks dans le localStorage.
 *
 * @constant
 * @type {string|null}
 */
const savedEmployees = localStorage.getItem('employees')

/**
 * Si des employs sont trouvs dans le localStorage, ils sont parss.
 * Sinon, utilise un tableau vide comme valeur initiale.
 *
 * @constant
 * @type {Array}
 */
const initialEmployees = savedEmployees ? JSON.parse(savedEmployees) : []

/**
 * tat initial du store, bas sur les donnes rcupres du localStorage ou
 * un tableau vide si aucune donne n'est trouve.
 *
 * @constant
 * @type {Object}
 */
const preloadedState = {
  employee: {
    employees: initialEmployees,
  },
}

/**
 * Configuration et cration du store Redux.
 *
 * @constant
 * @type {Object}
 */
const store = configureStore({
  reducer: {
    employee: employeeReducer,
  },
  preloadedState,
})

export default store
