import React from 'react'
import PropTypes from 'prop-types'
import HomeSVG from './assets/home-house.svg'
import CalendarSVG from './assets/calendar-alt.svg'
import ChevronDownSVG from './assets/chevron-down.svg'

/**
 * Icne de la maison.
 *
 * @returns {React.Element} - Renvoie l'icne de la maison.
 */
export function HomeIcon() {
  return <img src={HomeSVG} alt="Icne de la maison" width="18" height="18" />
}

/**
 * Icne du calendrier.
 *
 * @returns {React.Element} - Renvoie l'icne du calendrier.
 */
export function CalendarIcon() {
  return (
    <img src={CalendarSVG} alt="Icne du calendrier" width="18" height="18" />
  )
}

/**
 * Icne de la flche (chevron).
 *
 * @param {Object} props - Proprits du composant.
 * @param {string} props.direction - Direction de la flche (peut tre 'up', 'right', 'down' ou 'left').
 * @returns {React.Element} - Renvoie l'icne de la flche.
 */
export function ChevronIcon({ direction = 'down' }) {
  const rotationMap = {
    up: '0',
    right: '90',
    down: '180',
    left: '270',
  }

  const rotation = rotationMap[direction] || '0'

  return (
    <img
      src={ChevronDownSVG}
      alt="Icne de la flche"
      width="30"
      height="30"
      style={{ transform: `rotate(${rotation}deg)` }}
    />
  )
}

// PropTypes pour ChevronIcon
ChevronIcon.propTypes = {
  direction: PropTypes.oneOf(['up', 'right', 'down', 'left']),
}
