import React from 'react'
import styles from './AddEmployee.module.css'
import EmployeeForm from '../../components/EmployeeForm'

/**
 * Composant reprsentant la page d'ajout d'un employ.
 *
 * Ce composant affiche le formulaire permettant d'ajouter un nouvel employ.
 *
 * @function
 * @returns {JSX.Element} - L'lment JSX de la page d'ajout d'employ.
 */
const AddEmployee = () => (
  <main className={styles.homeContainer}>
    <EmployeeForm />
  </main>
)

export default AddEmployee
