import React from 'react'
import EmployeeTable from '../../components/EmployeeTable'
import styles from './Employees.module.css'
import employeeMock from '../../data/employeeMock'

/**
 * Composant reprsentant la page des employs actuels.
 *
 * @function
 * @returns {JSX.Element} - L'lment JSX reprsentant la liste des employs.
 */
const Employees = () => (
  <main className={`${styles.main} ${styles.bgDark}`}>
    {/* Titre de la page */}
    <h1 className={styles.title}>Employs actuels</h1>
    {/* Tableau des employs. Les donnes proviennent d'un mock. */}
    <EmployeeTable data={employeeMock} />
  </main>
)

export default Employees
