import { createSlice } from '@reduxjs/toolkit'
import employeeMock from '../data/employeeMock'

/**
 * tat initial du slice employee, bas sur un mock d'employs.
 *
 * @constant
 * @type {Object}
 */
const initialState = {
  employees: employeeMock,
}

/**
 * Slice Redux pour grer les actions et rductions des employs.
 *
 * @constant
 * @type {Object}
 */
const employeeSlice = createSlice({
  name: 'employee',
  initialState,
  reducers: {
    /**
     * Initialise la liste des employs.
     *
     * @param {Object} state - tat actuel.
     * @param {Object} action - Action contenant la nouvelle liste des employs.
     */
    initEmployees: (state, action) => {
      state.employees = action.payload
    },
    /**
     * Ajoute un nouvel employ  la liste et sauvegarde la liste mise  jour dans le localStorage.
     *
     * @param {Object} state - tat actuel.
     * @param {Object} action - Action contenant les informations du nouvel employ.
     */
    addEmployee: (state, action) => {
      state.employees.push(action.payload)
      localStorage.setItem('employees', JSON.stringify(state.employees))
    },
    /**
     * Charge la liste des employs  partir du localStorage.
     * Si aucune liste n'est trouve dans le localStorage, utilise le mock initial.
     *
     * @param {Object} state - tat actuel.
     */
    loadFromLocalStorage: (state) => {
      const savedEmployees = JSON.parse(localStorage.getItem('employees'))
      state.employees =
        savedEmployees && savedEmployees.length > 0
          ? savedEmployees
          : employeeMock
    },
  },
})

/**
 * Exporte les actions gnres par le slice.
 *
 * @constant
 * @type {Object}
 */
export const { initEmployees, addEmployee, loadFromLocalStorage } =
  employeeSlice.actions

/**
 * Exporte le rducteur gnr par le slice.
 *
 * @constant
 * @type {Function}
 */
export default employeeSlice.reducer
