/**
 * Valide les donnes du formulaire d'employ en vrifiant si chaque champ est rempli.
 * Si un champ n'est pas rempli, une erreur est gnre pour ce champ.
 *
 * @function
 * @param {Object} formData - Les donnes du formulaire  valider.
 * @param {Object} formattedFieldNames - Les noms de champs formats pour l'affichage (par ex. { firstName: 'Prnom' }).
 * @returns {Object} Un objet contenant les erreurs pour chaque champ non rempli.
 */
export const validateEmployeeForm = (formData, formattedFieldNames) => {
  let fieldErrors = {}

  for (let field in formData) {
    if (!formData[field]) {
      let formattedField = formattedFieldNames[field] || field
      fieldErrors[field] = `${formattedField} is required`
    }
  }

  return fieldErrors
}
